@font-face {
  font-family: Montserrat;
  src: url("Montserrat.7689b5bf.woff") format("woff");
}

html, body {
  justify-content: center;
  align-items: center;
  height: 100%;
  margin: 0;
  font-family: Montserrat;
  display: flex;
  overflow: hidden;
}

.container-marquesina {
  z-index: 99;
  background-color: #ffffffbf;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

p#marquesina {
  text-align: center;
  word-wrap: break-word;
  text-align: center;
  -webkit-text-fill-color: #0000004d;
  background: linear-gradient(-45deg, #4bc0c8 25%, #feac5e 25% 50%, #4bc0c8 50% 75%, #feac5e 75%, #feac5e) 0 0 / 10px 10px;
  -webkit-background-clip: text;
  place-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: Bungee Shade, cursive;
  font-size: 5vw;
  font-weight: normal;
  animation: 1s linear infinite stripes;
  display: flex;
  position: fixed;
}

@keyframes stripes {
  100% {
    background-position: 10x 0, 10px 0, 10px 0;
  }
}

#canvas {
  width: 100%;
  height: 100%;
  display: block;
}

#overlay {
  text-align: center;
  z-index: 98;
  background: #ffed94;
  width: 100vw;
  height: 100vh;
  font-size: 5vh;
  position: fixed;
}

.desvanecer {
  animation: 1s linear desvanecer;
}

@keyframes desvanecer {
  10% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

.ocultar {
  visibility: hidden;
}

#loading-indicator {
  margin-top: 15px;
  display: block;
}

#click-to-play {
  color: #000;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #000;
  margin-top: 30vh;
}

#click-to-play div {
  margin-top: 5vh;
  margin-bottom: 5vh;
}

#click-to-play button, #volver, #clue, #detener-marquesina {
  cursor: pointer;
  background-color: #ffed94;
  padding: 25px;
  font-family: Montserrat;
  font-size: 1.5rem;
  font-weight: 500;
}

#volver, #clue, #detener-marquesina {
  z-index: 97;
  padding: 10px;
  position: absolute;
  top: 0;
}

#detener-marquesina {
  z-index: 99;
